.fixed-bar {
  background-color: #7e318e;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 90px;
}
.fixed-bar .left-bar {
  margin-left: 280px;
  width: 400px;
  height: 100%;
  line-height: 90px;
}
.fixed-bar .left-bar p {
  font-size: 24px;
  color: #fff;
}
.fixed-bar .left-bar span {
  font-size: 40px;
  font-weight: bold;
  color: yellow;
}
.fixed-bar .right-bar {
  width: 400px;
  height: 100%;
}
.fixed-bar .right-bar button {
  margin: 21px 15px 0px 15px;
  width: 275px;
  height: 50px;
  background-color: #ffffff;
  font-size: 20px;
  color: #7e318e;
}
.fixed-bar .right-bar img {
  height: 51px;
  width: 51px;
  margin-top: 21px;
}
